
import { defineComponent, computed } from 'vue'
import { useStore } from '@/store'

import localCache from '@/utils/cache'

import { ElMessage } from 'element-plus'

import TlCard from '@/base-ui/card'

import { modalConfig } from './config/modal.config'
import PageModal from '@/components/page-modal'
import { usePageModal } from '@/hooks/usePageModal'

export default defineComponent({
  components: {
    TlCard,
    PageModal
  },
  setup() {
    const store = useStore()
    const userId = computed(() => store.state.login.userInfo.id)
    const avatarUrl = computed(() => store.state.login.userInfo.avatar_url)
    const userInfo = computed(() => store.state.login.userInfo)

    const token = localCache.getCache('token')
    const myheaders = {
      Authorization: `Bearer ${token}`
    }

    const handleAvatarSuccess = async () => {
      await store.dispatch('login/getUserInfo', {
        id: userId.value
      })
    }

    const beforeAvatarUpload = (file: any) => {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        ElMessage.error('Avatar picture must be JPG format!')
      }
      if (!isLt2M) {
        ElMessage.error('Avatar picture size can not exceed 2MB!')
      }
      return isJPG && isLt2M
    }

    const modalConfigComputed = computed(() => {
      const departmentItem = modalConfig.formItems.find(
        (item) => item.field === 'departmentId'
      )
      departmentItem!.options = store.state.entireDepartment.map((item) => {
        return { title: item.name, value: item.id }
      })
      const roleItem = modalConfig.formItems.find(
        (item) => item.field === 'roleId'
      )
      roleItem!.options = store.state.entireRole.map((item) => {
        return { title: item.name, value: item.id }
      })
      return modalConfig
    })

    const newCallBack = () => {
      const passwordItem = modalConfig.formItems.find(
        (item) => item.field === 'password'
      )
      passwordItem!.isHidden = false
    }
    const editCallBack = () => {
      const passwordItem = modalConfig.formItems.find(
        (item) => item.field === 'password'
      )
      passwordItem!.isHidden = true
      return {
        ...userInfo.value,
        departmentId: userInfo.value.department.id,
        roleId: userInfo.value.role.id
      }
    }

    // 3. 调用hook获取公共变量和函数
    const [pageModalRef, defaultInfo, handleNewData, handleEditData] =
      usePageModal(newCallBack, editCallBack)

    return {
      avatarUrl,
      myheaders,
      userInfo,
      handleAvatarSuccess,
      beforeAvatarUpload,
      modalConfigComputed,
      pageModalRef,
      defaultInfo,
      handleNewData,
      handleEditData
    }
  }
})
